.container{
    position: relative;
    width: 280px;
    height: 606px;
    padding-bottom: 32px;
    background-color: var(--btn-bg-default);
    box-shadow: 7px 4px 14px rgba(49, 21, 4, 0.07);
    border-radius: 0px 0px 20px 20px;   
}
@media screen and (min-width: 768px) {
    .container {
        width: 336px;
    }
}
@media screen and (min-width: 1280px) {
    .container{
    width: 288px;
    }
}
.imgWrapper{
    position: relative;
    width: 280px;
}
@media screen and (min-width: 768px) {
    .imgWrapper {
        width: 336px;
    }
}
@media screen and (min-width: 1280px) {
    .imgWrapper {
        width: 288px;
    }
}

.categoryLabel{
    position: absolute;
    top: 20px;
    left: 0;
    width: 158px;
    padding: 6px 20px;
    background-color: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(2px);
    border-radius: 0 20px 20px 0;
    font-size: 12px;
    line-height: 16px;
}
.infoWrapper{
    padding: 20px 20px 0px 20px;
}
.title{
    font-weight: 700;
    width: 231px;
    margin-bottom: 20px;
    font-size: 28px;
    line-height: 38px;
}
.infoList {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
    justify-content: space-between;
}
.label{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    line-height: 21.86px;
}
.lebalText{
    width: 50%;
    font-weight: 500;
    font-size: 16px;
    line-height: 21.86px;
}
.learnMoreBtnCont {
    width: 100%;
    position: absolute;
    bottom: 32px;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}
.learnMoreBtn{
    display: block;
    margin: 0 auto;
    width: 248px;
    height:  38px;
    padding: 8px 79px;
    background: var(--btn-bg-default);
    border: 2px solid var(--color-another);
    color: var(--color-another);
    border-radius: 40px;
    cursor: pointer;
    font-size: 16px;
}
.learmMoreBtn:hover{
    color: #ff6101;
    border: 2px solid #ff6101;
    transition: var(--transition);
}
.heartBtn {
    position: absolute;
    top: 12px;
    right: 12px;
    width: 44px;
    height: 44px;
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 0;
    background: rgba(253, 247, 242, 0.5);
    border-radius: 50%;
}
.heartBtn:hover {
    background: rgba(253, 247, 242, 1);
    transition: var(--transition);
}
.heartItemBtnActive {
    background: none;
    fill: var(--color-hover-active);
    border-radius: 50%;
}

.heartItemBtn {
    background: none;
    fill: rgba(175, 177, 184, 0.1);
    border-radius: 50%;
}