.navWrapper {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 66px;
  left: 0;
  z-index: 1;
  background-color: var(--bg);
}
@media screen and (min-width: 768px) {
  .navWrapper {
    top: 0;
    padding-top: 72px;
  }
}
@media screen and (max-width: 767px) {
  .navWrapper {
    padding-top: 18px;
  }
}
.authWrapper {
  display: flex;
  justify-content: center;
}
.authWrapper > ul,
.authWrapper > button {
  margin-left: 0;
}
.linkWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.link {
  font-family: 'Manrope';
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  line-height: 44px;
  letter-spacing: 0.04em;
  color: #181c27;
  transition: var(--transition);
}
.link:not(:last-child) {
  margin-bottom: 40px;
}
.link:first-child {
  margin-top: 60px;
}
.active {
  font-weight: 700;
  text-decoration-line: underline;
  color: #f59256;
}
.link:hover,
.link:focus {
  color: var(--color-hover-active);
}
